import CharmPartType from "./charmPart.type";
import styled from "styled-components";
import { CharmComponentMap } from "../../../const/CharmComponetMap";

const TopContainer = styled.div`
  position: absolute;
  top: 20px;
  left: 50%;

  transform: translateX(-50%);

  width: 200px;
  height: 150px;

  display: flex;
  align-items: center;
  justify-content: center;
`;

const CharmTopPart: React.FC<CharmPartType> = ({ id, kind, style, index }) => {
  const Component = CharmComponentMap[kind][style].top[index];
  return (
    <TopContainer id={id}>
      <Component />
    </TopContainer>
  );
};

export default CharmTopPart;
