import styled from "styled-components";
import { CharmKind, CharmKindMap } from "../../../../const/CharmData";
import { useRecoilState } from "recoil";
import { colorSetsState } from "../../../../atom/ColorAtom";
import ColorChip from "./ColorChip";
import { useCallback } from "react";

const Container = styled.div`
  width: 100%;
  height: 152px;

  padding: 0 20px;

  display: flex;
  align-items: center;
  gap: 8px;

  & > h4 {
    color: #454545;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 100%; /* 14px */
    letter-spacing: -0.028px;
  }
`;

const ChipContainer = styled.div`
  position: relative;
  width: 183px;
  height: 32px;

  display: flex;

  div:first-of-type {
    flex-grow: 2;
  }

  border: 1px solid #dedede;
`;

interface Prop {
  kind: CharmKind;
}

const ColorSetsList: React.FC<Prop> = ({ kind }) => {
  const [colorSets, setColorSets] = useRecoilState(colorSetsState);

  const changeColor = useCallback(
    (index: number) => (color: string) => {
      const now = [...colorSets[kind]];
      now[index] = color;
      setColorSets({
        ...colorSets,
        [kind]: [...now],
      });
    },
    [colorSets, setColorSets, kind]
  );

  return (
    <Container>
      <h4>{CharmKindMap[kind]}</h4>
      <ChipContainer>
        {colorSets[kind].map((color, index) => (
          <ColorChip
            key={color + index}
            color={color}
            changeColor={changeColor(index)}
          />
        ))}
      </ChipContainer>
    </Container>
  );
};

export default ColorSetsList;
