import { useRecoilState } from "recoil";
import {
  charmIndexState,
  charmKindState,
  charmStyleState,
  initialCharmIndex,
} from "../atom/CharmAtom";
import {
  backgroundColorSetState,
  colorSetsState,
  initialBackgroundColorSet,
  initialColorSets,
  selectedBackgroundIndexState,
} from "../atom/ColorAtom";
import { gradiantState, strokeWidthState } from "../atom/ControllAtom";
import _ from "lodash";
import { CharmKindList, CharmStyleList } from "./CharmData";

const useChanged = () => {
  const [kind, setKind] = useRecoilState(charmKindState);
  const [style, setStyle] = useRecoilState(charmStyleState);
  const [index, setIndex] = useRecoilState(charmIndexState);
  const [colorSets, setColorSets] = useRecoilState(colorSetsState);
  const [bgColorSet, setBgColorSet] = useRecoilState(backgroundColorSetState);
  const [bgSelect, setBgSelect] = useRecoilState(selectedBackgroundIndexState);
  const [strokeWidth, setStrokeWidth] = useRecoilState(strokeWidthState);
  const [gradiant, setGradiant] = useRecoilState(gradiantState);

  const isEqual =
    _.isEqual(kind, CharmKindList[0]) &&
    _.isEqual(style, CharmStyleList[0]) &&
    _.isEqual(index, initialCharmIndex) &&
    _.isEqual(colorSets, initialColorSets) &&
    _.isEqual(bgColorSet, initialBackgroundColorSet) &&
    _.isEqual(bgSelect, 0) &&
    _.isEqual(strokeWidth, 0) &&
    _.isEqual(gradiant, 0);

  const reset = () => {
    setKind(CharmKindList[0]);
    setStyle(CharmStyleList[0]);
    setIndex(initialCharmIndex);
    setColorSets(initialColorSets);
    setBgColorSet(initialBackgroundColorSet);
    setBgSelect(0);
    setStrokeWidth(0);
    setGradiant(0);
  };

  return {
    isEqual,
    reset,
  };
};

export default useChanged;
