import { default as RCSlider } from "rc-slider";
import "rc-slider/assets/index.css";
import styled from "styled-components";

const Container = styled.div`
  display: flex;
  align-items: center;
`;

const Label = styled.div`
  margin-right: 15px;

  color: #e1e1e1;

  user-select: none;

  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 150%; /* 27px */
  letter-spacing: -0.036px;
`;

interface Prop {
  label?: string;
  value: number;
  onChange: (value: number) => void;
}

const Slider: React.FC<Prop> = ({ label, onChange, value }) => {
  return (
    <Container>
      {label && <Label>{label}</Label>}
      <RCSlider
        value={value}
        min={0}
        max={100}
        // @ts-ignore
        onChange={onChange}
        style={{
          width: 270,
          height: 20,
          flexShrink: 0,
        }}
        trackStyle={{
          height: 6,
          backgroundColor: "white",
        }}
        handleStyle={{
          marginTop: -3.5,
          width: 14,
          height: 14,
          borderRadius: 14,
          background: "#F0F0F0",
          boxShadow: "0px 1px 4px 0px rgba(0, 0, 0, 0.25)",
          border: "none",
          opacity: 1,
        }}
        railStyle={{
          width: "100%",
          height: 6,
          flexShrink: 0,
          borderRadius: 5,
          border: "1px solid #8F8F8F",
          backgroundColor: "transparent",
        }}
      />
    </Container>
  );
};

export default Slider;
