import styled from "styled-components";
import Slider from "../component/Slider/Slider";
import ColorController from "../component/Color/ColorController";
import { useRecoilState } from "recoil";
import { gradiantState, strokeWidthState } from "../atom/ControllAtom";

const Container = styled.div`
  position: absolute;
  bottom: 0;
  width: 100vw;
  height: 100px;

  display: flex;
  justify-content: space-around;
`;

const Section = styled.div`
  flex-grow: 1;
  flex-shrink: 0;
  width: 300px;
  height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
`;

const BottomController = () => {
  const [strokeWidth, setStrokeWidth] = useRecoilState(strokeWidthState);
  const [gradiant, setGradiant] = useRecoilState(gradiantState);
  return (
    <Container>
      <Section>
        <ColorController />
      </Section>
      <Section>
        <Slider
          label="Stroke width"
          value={strokeWidth}
          onChange={setStrokeWidth}
        />
      </Section>
      <Section>
        <Slider label="Gradiant" value={gradiant} onChange={setGradiant} />
      </Section>
    </Container>
  );
};

export default BottomController;
