import CharmPartType from "./charmPart.type";
import styled from "styled-components";

import { CharmComponentMap } from "../../../const/CharmComponetMap";

const BottomContainer = styled.div`
  position: absolute;
  bottom: 15px;
  left: 50%;

  transform: translateX(-50%);

  width: 160px;
  height: 140px;

  display: flex;
  align-items: center;
  justify-content: center;

  & > svg {
    width: 100%;
  }
`;

const CharmBottomPart: React.FC<CharmPartType> = ({
  id,
  kind,
  style,
  index,
}) => {
  const Component = CharmComponentMap[kind][style].bot[index];

  return (
    <BottomContainer id={id}>
      <Component />
    </BottomContainer>
  );
};

export default CharmBottomPart;
