import styled from "styled-components";
import {
  HealthButton,
  LoveButton,
  MoneyButton,
} from "../component/Button/ButtonList";
import { useRecoilState } from "recoil";
import { charmKindState } from "../atom/CharmAtom";

export type LeftButton = "MONEY" | "LOVE" | "HEALTH";

const ButtonListContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 71px;
`;

const LeftController = () => {
  const [selectedCharmKind, setSelectedCharmKind] =
    useRecoilState(charmKindState);

  return (
    <ButtonListContainer>
      <MoneyButton
        isActive={selectedCharmKind === "MONEY"}
        onClick={() => setSelectedCharmKind("MONEY")}
      />
      <LoveButton
        isActive={selectedCharmKind === "LOVE"}
        onClick={() => setSelectedCharmKind("LOVE")}
      />
      <HealthButton
        isActive={selectedCharmKind === "HEALTH"}
        onClick={() => setSelectedCharmKind("HEALTH")}
      />
    </ButtonListContainer>
  );
};

export default LeftController;
