import ColorLabel from "../ColorLabel";
import BackgroundColorChip from "./BackgroundColorChip";
import { useRecoilState, useRecoilValue } from "recoil";
import {
  backgroundColorSetState,
  selectedBackgroundIndexState,
} from "../../../atom/ColorAtom";

const BackgroundColorController = () => {
  const backgroundColorSet = useRecoilValue(backgroundColorSetState);
  const [selectedIndex, setSelectedIndex] = useRecoilState(
    selectedBackgroundIndexState
  );

  const handleClickColor = (index: number) => setSelectedIndex(index);

  return (
    <ColorLabel label="Background">
      {backgroundColorSet.map((color, index) => (
        <BackgroundColorChip
          key={color + index}
          isSelected={index === selectedIndex}
          onClick={() => handleClickColor(index)}
          color={color}
        />
      ))}
    </ColorLabel>
  );
};

export default BackgroundColorController;
