import styled from "styled-components";
import { useRecoilState } from "recoil";
import { backgroundColorSetState } from "../../../../atom/ColorAtom";
import BackgroundColorChip from "./BackgroundColorChip";

const Container = styled.div`
  position: relative;
  width: 100%;
  padding-left: 18px;
  padding-right: 22px;
  margin-top: 14px;
  margin-bottom: 19px;

  display: flex;
  align-items: center;
  gap: 8px;

  & > h4 {
    width: 37px;
    color: #454545;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 100%; /* 14px */
    letter-spacing: -0.028px;
  }
`;

const ColorContainer = styled.div`
  width: 183px;

  display: grid;
  grid-template-columns: repeat(4, 40px);
  grid-template-rows: repeat(2, 30px);
  gap: 8px;
`;

const BackgroundColorSet = () => {
  const [backgroundColorSet, setBackgroundColorSet] = useRecoilState(
    backgroundColorSetState
  );

  const changeColor = (index: number) => (color: string) => {
    const now = [...backgroundColorSet];
    now[index] = color;
    setBackgroundColorSet(now);
  };

  return (
    <Container>
      <h4>배경</h4>
      <ColorContainer>
        {backgroundColorSet.map((color, index) => (
          <BackgroundColorChip
            key={color + index}
            color={color}
            changeColor={changeColor(index)}
          />
        ))}
      </ColorContainer>
    </Container>
  );
};

export default BackgroundColorSet;
