import { atom } from "recoil";
import {
  CharmKind,
  CharmKindList,
  CharmStyle,
  CharmStyleList,
} from "../const/CharmData";

export const charmKindState = atom<CharmKind>({
  key: "CHARM_KIND",
  default: CharmKindList[0],
});

export const charmStyleState = atom<CharmStyle>({
  key: "CHARM_STYLE",
  default: CharmStyleList[0],
});

export const initialCharmIndex = { top: 0, middle: 0, bottom: 0 };

export const charmIndexState = atom<{
  top: number;
  middle: number;
  bottom: number;
}>({
  key: "CHARM_INDEX",
  default: initialCharmIndex,
});
