import styled from "styled-components";
import {
  LeftBottomButton,
  LeftMiddleButton,
  LeftTopButton,
  RightBottomButton,
  RightMiddleButton,
  RightTopButton,
} from "../Button/ButtonList";
import { useRecoilState, useRecoilValue } from "recoil";
import {
  charmIndexState,
  charmKindState,
  charmStyleState,
} from "../../atom/CharmAtom";
import CharmTopPart from "./Part/CharmTopPart";
import CharmMiddlePart from "./Part/CharmMiddlePart";
import CharmBottomPart from "./Part/CharmBottomPart";
import { useEffect } from "react";

const Container = styled.div`
  position: relative;
`;

const Background = styled.div`
  position: relative;
  width: 262px;
  height: 600px;
  background: #171589;

  border-radius: 5px;

  transition: all 0.3s ease;
`;

const LeftSection = styled.div`
  position: absolute;
  top: 50%;
  left: -100px;
  transform: translateY(-50%);

  height: 50vh;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
`;

const RightSection = styled.div`
  position: absolute;
  top: 50%;
  right: -100px;
  transform: translateY(-50%);

  height: 50vh;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
`;

type ButtonDirection = "LEFT" | "RIGHT";

const Charm = () => {
  const charmKind = useRecoilValue(charmKindState);
  const charmStyle = useRecoilValue(charmStyleState);
  const [charmIndex, setCharmIndex] = useRecoilState(charmIndexState);

  useEffect(() => {
    setCharmIndex({ top: 0, middle: 0, bottom: 0 });
  }, [charmKind, charmStyle]);

  const handleTopClick = (direction: ButtonDirection) => () => {
    if (direction === "LEFT")
      setCharmIndex({
        ...charmIndex,
        top: charmIndex.top === 0 ? 3 : charmIndex.top - 1,
      });
    if (direction === "RIGHT")
      setCharmIndex({
        ...charmIndex,
        top: charmIndex.top === 3 ? 0 : charmIndex.top + 1,
      });
  };

  const handleMiddleClick = (direction: ButtonDirection) => () => {
    if (direction === "LEFT")
      setCharmIndex({
        ...charmIndex,
        middle: charmIndex.middle === 0 ? 3 : charmIndex.middle - 1,
      });
    if (direction === "RIGHT")
      setCharmIndex({
        ...charmIndex,
        middle: charmIndex.middle === 3 ? 0 : charmIndex.middle + 1,
      });
  };

  const handleBottomClick = (direction: ButtonDirection) => () => {
    if (direction === "LEFT")
      setCharmIndex({
        ...charmIndex,
        bottom: charmIndex.bottom === 0 ? 2 : charmIndex.bottom - 1,
      });
    if (direction === "RIGHT")
      setCharmIndex({
        ...charmIndex,
        bottom: charmIndex.bottom === 2 ? 0 : charmIndex.bottom + 1,
      });
  };

  return (
    <Container>
      <LeftSection>
        <LeftTopButton onClick={handleTopClick("LEFT")} />
        <LeftMiddleButton onClick={handleMiddleClick("LEFT")} />
        <LeftBottomButton onClick={handleBottomClick("LEFT")} />
      </LeftSection>
      <Background id="charm-background">
        <CharmTopPart
          id="charm-top"
          kind={charmKind}
          style={charmStyle}
          index={charmIndex.top}
        />
        <CharmMiddlePart
          id="charm-middle"
          kind={charmKind}
          style={charmStyle}
          index={charmIndex.middle}
        />
        <CharmBottomPart
          id="charm-bottom"
          kind={charmKind}
          style={charmStyle}
          index={charmIndex.bottom}
        />
      </Background>
      <RightSection>
        <RightTopButton onClick={handleTopClick("RIGHT")} />
        <RightMiddleButton onClick={handleMiddleClick("RIGHT")} />
        <RightBottomButton onClick={handleBottomClick("RIGHT")} />
      </RightSection>
    </Container>
  );
};

export default Charm;
