import styled from "styled-components";
import { toPng } from "html-to-image";
import { useCallback, useState } from "react";
import useChanged from "../../const/useChanged";

const Button = styled.button`
  all: unset;

  width: 120px;
  height: 36px;
  padding-top: 1px;

  display: flex;
  align-items: center;
  justify-content: center;

  border-radius: 5px;
  background-color: #8c8c8c;

  cursor: pointer;

  &:hover {
    background-color: #daff8c;
  }

  color: #151515;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  letter-spacing: -0.04px;

  transition: all 0.3s ease;

  user-select: none;
`;

const Tooltip = styled.div`
  position: absolute;
  top: calc(100% + 10px);
  right: 0;

  width: 230px;
  height: 70px;

  display: flex;
  align-items: center;
  justify-content: center;

  border-radius: 5px;
  background: #36353c;
  border: 1px solid #daff8c;

  opacity: 0.8;

  animation: fadein 0.3s;

  @keyframes fadein {
    from {
      top: calc(100% + 20px);
      opacity: 0;
    }
    to {
      top: calc(100% + 10px);
      opacity: 0.8;
    }
  }

  & > p {
    display: block;
    width: fit-content;
    color: #c7c7c7;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 24px */
    letter-spacing: -0.032px;
  }
`;

const Complete = () => {
  const [isHover, setIsHover] = useState(false);

  const handleClick = useCallback(() => {
    const charmBackground = document.querySelector(
      "#charm-background"
    ) as HTMLDivElement;
    toPng(charmBackground, { cacheBust: true })
      .then((dataUrl) => {
        const link = document.createElement("a");
        link.download = "charm.png";
        link.href = dataUrl;
        link.click();
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  const { isEqual } = useChanged();

  return (
    <Button
      onClick={handleClick}
      onMouseOver={() => setIsHover(true)}
      onMouseLeave={() => setIsHover(false)}
    >
      <span>저장하기</span>
      {isEqual && isHover && (
        <Tooltip>
          <p>
            그래픽을 조합하고 수치를
            <br />
            변경하여 오늘의 운세를 높혀보세요.
          </p>
        </Tooltip>
      )}
    </Button>
  );
};

export default Complete;
