import { HexColorPicker } from "react-colorful";
import styled from "styled-components";
import { useCallback, useEffect, useRef, useState } from "react";

const Container = styled.div`
  position: absolute;
  top: -240px;

  width: 210px;
  height: 230px;

  box-sizing: border-box;
  padding: 5px;

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;

  background-color: white;
  border-radius: 10px;
  border: 1px solid lightgrey;

  z-index: 100;
`;

const ButtonContainer = styled.div`
  position: relative;
  width: 100%;
  display: flex;
  gap: 5px;

  button:first-of-type {
    flex-grow: 4;
  }
`;

const Button = styled.button`
  all: unset;
  flex-grow: 1;

  padding: 1px;
  vertical-align: center;
  text-align: center;

  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 10px;

  user-select: none;

  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%; /* 27px */
  letter-spacing: -0.036px;
`;

interface Prop {
  nowColor: string;
  changeColor: (color: string) => void;
  closePicker: () => void;
}

const ColorPicker: React.FC<Prop> = ({
  nowColor,
  changeColor,
  closePicker,
}) => {
  const [color, setColor] = useState(nowColor);

  const colorPickerRef = useRef<HTMLDivElement>(null);

  const handleClickConfirm = useCallback(() => {
    closePicker();
    if (color !== nowColor) changeColor(color);
  }, [changeColor, closePicker, color, nowColor]);

  useEffect(() => {
    const colorPicker = colorPickerRef.current;
    if (colorPicker) {
      const clickEventHandler = (e: MouseEvent) => {
        const target = e.target as HTMLElement;
        const isCollect = colorPicker.parentNode!.contains(target);
        if (!isCollect || target.id === "picker-cancel") closePicker();
      };
      document.addEventListener("click", clickEventHandler);
      return () => document.removeEventListener("click", clickEventHandler);
    }
  }, [handleClickConfirm, colorPickerRef, closePicker]);

  return (
    <Container ref={colorPickerRef}>
      <HexColorPicker color={color} onChange={setColor} />
      <ButtonContainer>
        <Button
          id="picker-confirm"
          style={{ backgroundColor: color }}
          onClick={handleClickConfirm}
        >
          😃
        </Button>
        <Button
          id="picker-cancel"
          style={{ backgroundColor: nowColor }}
          onClick={closePicker}
        >
          😅
        </Button>
      </ButtonContainer>
    </Container>
  );
};

export default ColorPicker;
