import styled from "styled-components";

const ColorChipContainer = styled.div`
  position: relative;
  height: 100%;
  flex-shrink: 0;
  flex-grow: 1;
`;

interface Prop {
  color: string;
}

const ColorChip: React.FC<Prop> = ({ color }) => {
  return <ColorChipContainer style={{ backgroundColor: color }} />;
};

export default ColorChip;
