import {
  Style1Button,
  Style2Button,
  Style3Button,
} from "../component/Button/ButtonList";
import { charmStyleState } from "../atom/CharmAtom";
import { useRecoilState } from "recoil";
import styled from "styled-components";
import Reset from "../component/Reset/Reset";
import Complete from "../component/Complete/Complete";

export type RightButton = "STYLE1" | "STYLE2" | "STYLE3";

const ButtonListContainer = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 71px;
`;

const TopContainer = styled.div`
  position: absolute;

  top: -170px;
  right: 0;

  display: flex;
  gap: 19px;
  align-items: center;
`;

const RightController = () => {
  const [selectedCharmStyle, setSelectedCharmStyle] =
    useRecoilState(charmStyleState);

  return (
    <ButtonListContainer>
      <Style1Button
        isActive={selectedCharmStyle === "STYLE1"}
        onClick={() => setSelectedCharmStyle("STYLE1")}
      />
      <Style2Button
        isActive={selectedCharmStyle === "STYLE2"}
        onClick={() => setSelectedCharmStyle("STYLE2")}
      />
      <Style3Button
        isActive={selectedCharmStyle === "STYLE3"}
        onClick={() => setSelectedCharmStyle("STYLE3")}
      />
      <TopContainer>
        <Reset />
        <Complete />
      </TopContainer>
    </ButtonListContainer>
  );
};

export default RightController;
