import { atom } from "recoil";
import { CharmKind } from "../const/CharmData";

type BackgroundColorSet = Record<CharmKind, string[]>;

export const initialColorSets: BackgroundColorSet = {
  MONEY: ["#FFCF08", "#FF6836", "#404272", "#2AAEB3"],
  LOVE: ["#FA4013", "#FFFFFF", "#9CD323", "#4F4085"],
  HEALTH: ["#327EED", "#2032AC", "#FFCF08", "#FEA39C"],
};

export const colorSetsState = atom<BackgroundColorSet>({
  key: "COLOR_SETS",
  default: initialColorSets,
});

export const initialBackgroundColorSet: string[] = [
  "#070580",
  "#FDFF85",
  "#3F3F3F",
  "#004AB9",
  "#FF5EB9",
  "#362C4C",
  "#ECE3E2",
  "#FFE6BA",
];

export const backgroundColorSetState = atom<string[]>({
  key: "BACKGROUND_COLOR_SET",
  default: initialBackgroundColorSet,
});

export const initialSelectedBackgroundIndex = 0;

export const selectedBackgroundIndexState = atom<number>({
  key: "SELECTED_BACKGROUND_INDEX",
  default: initialSelectedBackgroundIndex,
});
