import ColorLabel from "../ColorLabel";
import ColorSet from "./ColorSet";
import { useRecoilState, useRecoilValue } from "recoil";
import { colorSetsState } from "../../../atom/ColorAtom";
import { charmKindState } from "../../../atom/CharmAtom";
import { CharmKindList } from "../../../const/CharmData";

const ColorSetController = () => {
  const colorSets = useRecoilValue(colorSetsState);
  const [charmKind, setCharmKind] = useRecoilState(charmKindState);

  return (
    <ColorLabel label="Color">
      {CharmKindList.map((kind) => (
        <ColorSet
          key={kind}
          colorSet={colorSets[kind]}
          isSelected={kind === charmKind}
          onClick={() => setCharmKind(kind)}
        />
      ))}
    </ColorLabel>
  );
};

export default ColorSetController;
