import {
  initialBackgroundColorSet,
  initialColorSets,
  initialSelectedBackgroundIndex,
} from "../atom/ColorAtom";
import { CharmKindList } from "../const/CharmData";
import { addLinearGradientToDefs } from "./Event.util";

const styleData = {
  strokeWidth: 1,
  gradient: 0,
  colorSet: initialColorSets[CharmKindList[0]],
  backgroundColor: initialBackgroundColorSet[initialSelectedBackgroundIndex],
  gradientInitColor: "white",
};

const backgroundRender = () => {
  const charmBackground = document.querySelector(
    "#charm-background"
  ) as HTMLDivElement;
  charmBackground.style.backgroundColor = styleData.backgroundColor;
};

export const charmRender = () => {
  // Charm
  const charmTopPart = document.querySelector("#charm-top") as HTMLDivElement;
  const charmMiddlePart = document.querySelector(
    "#charm-middle"
  ) as HTMLDivElement;
  const charmBottomPart = document.querySelector(
    "#charm-bottom"
  ) as HTMLDivElement;

  const charmParts = [charmTopPart, charmMiddlePart, charmBottomPart];
  charmParts.forEach((part) => {
    const svg = part.children[0] as SVGSVGElement;
    // defs remove
    svg.querySelector("defs")?.remove();

    const defs = document.createElementNS(svg.namespaceURI, "defs");

    // gradient
    styleData.colorSet.forEach((color, index) => {
      addLinearGradientToDefs(
        defs,
        `color-${index + 1}`,
        color,
        styleData.gradientInitColor,
        styleData.gradient
      );
    });

    // add class
    const style = document.createElementNS(svg.namespaceURI, "style");
    const styleClasses = [];

    // stroke-width
    const strokeWidth = `.stroke-width { stroke-width : ${styleData.strokeWidth}; } \n`;
    styleClasses.push(strokeWidth);

    styleData.colorSet.forEach((color, index) => {
      const colorClass = `.stroke-color-${index + 1} { stroke : ${color} } \n`;
      styleClasses.push(colorClass);
    });
    style.innerHTML = styleClasses.join("");
    defs.append(style);
    svg.append(defs);
  });
};

export const changeStrokeWidth = (value: number) => {
  const minWidth = 1;
  const maxWidth = 7;
  styleData.strokeWidth = 1 + ((maxWidth - minWidth) * value) / 100;
  charmRender();
};

export const changeGradient = (value: number) => {
  styleData.gradient = value;
  charmRender();
};

export const changeColorSet = (colorSet: string[]) => {
  styleData.colorSet = colorSet;
  charmRender();
};

export const changeBackgroundColor = (backgroundColor: string) => {
  styleData.backgroundColor = backgroundColor;
  backgroundRender();
};
