import { useState } from "react";
import ColorPicker from "../../ColorPicker";

interface Prop {
  color: string;
  changeColor: (color: string) => void;
}

const BackgroundColorChip: React.FC<Prop> = ({ color, changeColor }) => {
  const [showPicker, setShowPicker] = useState(false);

  return (
    <div style={{ backgroundColor: color }} onClick={() => setShowPicker(true)}>
      {showPicker && (
        <ColorPicker
          nowColor={color}
          changeColor={changeColor}
          closePicker={() => setShowPicker(false)}
        />
      )}
    </div>
  );
};

export default BackgroundColorChip;
