import { PropsWithChildren, useEffect } from "react";
import { useRecoilValue } from "recoil";
import {
  backgroundColorSetState,
  colorSetsState,
  selectedBackgroundIndexState,
} from "../atom/ColorAtom";
import {
  changeBackgroundColor,
  changeColorSet,
  changeGradient,
  changeStrokeWidth,
  charmRender,
} from "./Event";
import {
  charmIndexState,
  charmKindState,
  charmStyleState,
} from "../atom/CharmAtom";
import { gradiantState, strokeWidthState } from "../atom/ControllAtom";

const EventProvider: React.FC<PropsWithChildren> = ({ children }) => {
  useEffect(() => {
    charmRender();
  }, []);

  const backgroundIndex = useRecoilValue(selectedBackgroundIndexState);
  const backgroundColorSet = useRecoilValue(backgroundColorSetState);

  useEffect(() => {
    changeBackgroundColor(backgroundColorSet[backgroundIndex]);
  }, [backgroundIndex, backgroundColorSet]);

  const colorSet = useRecoilValue(colorSetsState);
  const charmIndex = useRecoilValue(charmIndexState);
  const charmStyle = useRecoilValue(charmStyleState);
  const charmKind = useRecoilValue(charmKindState);

  useEffect(() => {
    changeColorSet(colorSet[charmKind]);
  }, [colorSet, charmStyle, charmKind, charmIndex]);

  const gradiant = useRecoilValue(gradiantState);

  useEffect(() => {
    changeGradient(gradiant);
  }, [gradiant]);

  const strokeWidth = useRecoilValue(strokeWidthState);

  useEffect(() => {
    changeStrokeWidth(strokeWidth);
  }, [strokeWidth]);

  return <>{children}</>;
};

export default EventProvider;
