export const addStopToLinerGradient = (
  gradient: Element,
  color: string,
  offset: number
) => {
  const stop = document.createElementNS(gradient.namespaceURI, "stop");
  stop.setAttribute("stop-color", `${color}`);
  stop.setAttribute("offset", `${offset}%`);
  gradient.appendChild(stop);
};

export const addLinearGradientToDefs = (
  defs: Element,
  id: string,
  topColor: string,
  bottomColor: string,
  percent: number
) => {
  const gradient = document.createElementNS(
    defs.namespaceURI,
    "linearGradient"
  );
  gradient.setAttribute("id", id);
  gradient.setAttribute("gradientTransform", "rotate(90)");
  addStopToLinerGradient(gradient, topColor, 0);
  addStopToLinerGradient(gradient, topColor, 100 - percent);
  addStopToLinerGradient(gradient, bottomColor, 100);
  defs.appendChild(gradient);
};
