import styled from "styled-components";
import ColorSets from "./ColorSets/ColorSets";
// @ts-ignore
import { ReactComponent as CloseIcon } from "../../../assets/button/CloseIcon.svg";
import BackgroundColorSet from "./BackgroundColorSet/BackgroundColorSet";
import { useEffect, useRef } from "react";

const Container = styled.div`
  position: absolute;
  top: -320px;
  left: 100px;

  width: 268px;

  display: flex;
  flex-direction: column;

  border-radius: 10px;
  background: #fff;
  border: 1px solid lightgrey;

  animation: fadein 0.3s;

  @keyframes fadein {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  cursor: default;

  z-index: 1000;
`;

const Header = styled.div`
  position: relative;

  width: 100%;
  height: 56px;
  box-sizing: border-box;
  padding: 14px 20px 9px 14px;
  color: #707070;

  & > h1 {
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 100%; /* 16px */
    letter-spacing: -0.032px;
  }

  & > span {
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%; /* 13px */
    letter-spacing: -0.026px;
  }

  & > button {
    all: unset;

    position: absolute;
    top: 14px;
    right: 14px;

    cursor: pointer;
  }
`;

interface Prop {
  closePalette: () => void;
}

const Divider = styled.div`
  display: block;
  width: 100%;
  height: 1px;
  margin: 0;
  padding: 0;
  background: #eaeaea;
`;

const ColorPalette: React.FC<Prop> = ({ closePalette }) => {
  const colorPaletteRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const colorPalette = colorPaletteRef.current;
    if (colorPalette) {
      const colorController = document.body.querySelector("#color-controller");
      const clickEventHandler = (e: MouseEvent) => {
        const target = e.target as HTMLElement;
        const isContain = colorController!.contains(target);
        // TODO: 임시로 넣음
        if (target.id === "picker-confirm") return;
        if (!isContain) closePalette();
      };
      document.addEventListener("click", clickEventHandler);
      return () => document.removeEventListener("click", clickEventHandler);
    }
  }, [closePalette, colorPaletteRef]);

  const handleClose = (e: React.MouseEvent<HTMLButtonElement>) => {
    e.stopPropagation();
    closePalette();
  };

  return (
    <Container ref={colorPaletteRef}>
      <Header>
        <h1>Color Palette</h1>
        <span>색상을 수정하고 운을 높여보세요</span>
        <button onClick={handleClose}>
          <CloseIcon />
        </button>
      </Header>
      <Divider />
      <ColorSets />
      <Divider />
      <BackgroundColorSet />
    </Container>
  );
};

export default ColorPalette;
